import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams ,AlertController} from 'ionic-angular';
import * as firebase from 'firebase/app';
import { Platform } from 'ionic-angular';
import { Facebook } from '@ionic-native/facebook';
import { AngularFireAuth } from 'angularfire2/auth';
import { Storage } from '@ionic/storage';

/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private storage: Storage,
    private fb: Facebook,
    private platform: Platform,
    private afAuth: AngularFireAuth,
    public alertCtrl : AlertController,
  ) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LoginPage');
  }

  presentAlert() {
    let alert = this.alertCtrl.create({
      title: 'Error',
      subTitle: 'Intentelo nuevamente',
      buttons: ['Dismiss']
    });
    alert.present();
  }

  facebook(){
    if (this.platform.is('cordova')) {
      return this.fb.login(['email', 'public_profile']).then(res => {
        const facebookCredential = firebase.auth.FacebookAuthProvider.credential(res.authResponse.accessToken);
        return firebase.auth().signInWithCredential(facebookCredential).then(result =>{
          this.storage.set('Loged', true);
          this.navCtrl.setRoot('IntroPage', {}, {
            animate: true,
            direction: 'forward'
          });
        })
          .catch(result=>{
            this.presentAlert();
          });
      });
    }else{
      this.afAuth.auth.signInWithPopup(new firebase.auth.FacebookAuthProvider).then(x=>{
          if (x){
            this.storage.set('Loged', true);
            this.navCtrl.setRoot('IntroPage', {}, {
              animate: true,
              direction: 'forward'
            });
          }
        }
      );
    }
  }


  google(){
    this.presentAlert();
  }


}



